	getUsersAD(users, cb, opts) {
		var user;
		if (typeOf(cb) !== 'function') {
			console.log('cb is not a function');
			return
		};
		if (!users) {
			console.log('No users to get');
			cb(null);
			return;
		};
		!opts && (opts = {});
		if (typeOf(users) !== 'array') users = [users];
		var it = this;
		var userValue = '';
		var camlOrChunk = '<IsNull><FieldRef Name="ID"/></IsNull>';
		var usersFlat = [];
		var CHUNK_MAX_LENGTH = 500;
		(function flatternArrayR(items) {
			var item;
			for (var i = 0; i < items.length; i++) {
				item = items[i];
				typeOf(item) === 'array' ? flatternArrayR(item) : usersFlat.push(item);
			}
		}(users));
		var uid;
		var usersFlatLength = usersFlat.length;
		var usersFlatChunksCount = Math.ceil(usersFlatLength / CHUNK_MAX_LENGTH);
		var itemIndex = 0;
		var check;
		for (var chunkIndex = 1; chunkIndex <= usersFlatChunksCount; chunkIndex++) {
			var chunkEndIndex = usersFlatLength < chunkIndex * CHUNK_MAX_LENGTH ? usersFlatLength % CHUNK_MAX_LENGTH : CHUNK_MAX_LENGTH;
			for (var i = 0; i < chunkEndIndex; i++) {
				user = usersFlat[itemIndex++];
				switch ((user).constructor.getName()) {
					case 'String':
					case 'Number':
						userValue += '<Value Type="Number">' + user + '</Value>';
						break;
					case 'Object':
						user.uid && (userValue += '<Value Type="Number">' + user.uid + '</Value>');
						break;
					case 'SP.User':
						userValue += '<Value Type="Number">' + user.get_id() + '</Value>';
						break;
					case 'SP.FieldUserValue':
						userValue += '<Value Type="Number">' + user.get_lookupId() + '</Value>';
						break;
					case 'SP.ListItem':
						uid = user.get_item('uid');
						uid && (userValue += '<Value Type="Number">' + uid + '</Value>');
						break;
				}
			}
			camlOrChunk = '<Or>' + camlOrChunk + '<In><FieldRef Name="uid"/><Values>' + userValue + '</Values></In></Or>';
			userValue = '';
		}

		opts.caml = '<View><Query><Where>' + camlOrChunk + '</Where></Query></View>';
		this.getListItems('/AM/UsersAD', function(usersData) {
			cb && cb(usersData);
		}, opts);
	}

