	checkUserNames() {
		this.getListItems('/AM/UsersAD', function(usersData) {
			var usersStat = {};
			var abnormalTitles = [];
			var invalidTitles = [];
			var invalidIds = [];
			var shortTitles = [];
			for (var i = 0; i < usersData.length; i++) {
				userData = usersData[i];
				if (userData.Title) {
					titleSplitsLength = userData.Title.trim().split(/\s\s*/).length;
					if (!usersStat[titleSplitsLength]) {
						usersStat[titleSplitsLength] = 0;
					}
					usersStat[titleSplitsLength]++;
					if (titleSplitsLength !== 3) {
						abnormalTitles.push(userData.Title);
						if (/\s\s+|\s$/g.test(userData.Title)) {
							invalidTitles.push(userData.Title)
						}
						if (titleSplitsLength === 2) {
							shortTitles.push(userData.Title);
						}
					}
				} else {
					usersStat[0]++;
					invalidIds.push(userData.ID);
				}
			};
			console.log(usersStat);
			console.log(abnormalTitles);
			console.log(invalidTitles);
			console.log(invalidIds);
			console.log(shortTitles);
		})
	}

