	//

	getListAggregations(listData, columns, cb, opts) {
		var folder, column, typeOfColumn;
		var caml = '';
		var columnsCaml = '<IsNotNull><FieldRef Name="ID"/></IsNotNull>';
		var columnCaml = '';
		var camlQuery = '<IsNotNull><FieldRef Name="ID"/></IsNotNull>';
		if (opts) {
			folder = opts.folder;
			caml = opts.caml;
		}
		folder = folder ? '<Eq><FieldRef Name="FileDirRef"></FieldRef><Value Type="Lookup">' + this.formatFolderRelativeUrl(listData, opts) + '</Value></Eq>' : '';
		var fieldRefs = '';
		if (typeOf(columns) !== 'object') return;
		listData = typeOf(listData) === 'string' ? this.getListData(listData) : listData;
		for (var title in columns) {
			column = columns[title];
			typeOfColumn = typeOf(column);
			if (typeOfColumn === 'string') {
				fieldRefs += '<FieldRef Name="' + title + '" Type="' + column + '"/>'
			} else if (typeOfColumn === 'object') {
				fieldRefs += '<FieldRef Name="' + title + '" Type="' + column.type + '"/>';
				if (column.caml) {
					columnsCaml = '<IsNull><FieldRef Name="ID"/></IsNull>';
					if (typeOf(column.caml) === 'object') {
						columnCaml = this.getCamlQuery(column.caml);
					} else {
						columnCaml = this.parseCamlString(column.caml);
					}
					columnsCaml = '<Or>' + columnsCaml + columnCaml + '</Or>'
				}
			}
		}
		if (folder && caml) {
			camlQuery = '<And>' + folder + caml + '</And>';
		} else {
			camlQuery = folder ? folder : caml;
		}

		if (columnsCaml) {
			if (camlQuery) {
				camlQuery = '<And>' + camlQuery + columnsCaml + '</And>';
			} else {
				camlQuery = columnsCaml;
			}
		}
		var clientContext = new SP.ClientContext(listData.path);
		var lists = clientContext.get_web().get_lists();
		var list = this.constants.GUID_MASK.test(listData.title) ?
			lists.getById(listData.title) :
			lists.getByTitle(listData.title);
		var camlResult = '\
			<View Scope="RecursiveAll">\
				<Query><Where>' + camlQuery + '</Where></Query>\
				<Aggregations>' + fieldRefs + '</Aggregations>\
			</View>'
		var aggregationsQuery = list.renderListData(camlResult);
		this.execute(clientContext, function(sender, args) {
			var aggregationsData = JSON.parse(aggregationsQuery.get_value()).Row[0];
			// console.log(aggregationsQuery.get_value());
			var aggregations = {};
			for (var columnName in columns) {
				aggregations[columnName] = 0;
			};
			for (var name in aggregationsData) {
				splittedName = name.split('.')[0];
				if (columns.hasOwnProperty(splittedName)) {
					aggregations[splittedName] = ~~aggregationsData[name];
				}
			}
			cb && cb(aggregations);
		}, this.onQueryFailed)
	}

